.Movies {
  text-align: center;
  padding: 25px;
  min-height: 100vh;
  max-width: 1500vh;
  background: inherit;
  color: #000;
  perspective: 800px;
}

.Movies h1 {
  font-size: 52px;
  margin: 0;
}
.Movies h2 {
  color: #F6C945;
  text-transform: uppercase;
}

.Movies img {
  width: 240px;
  height: 180px;
  cursor: grab;
  margin-right: 10px;
  display: inline-grid;
}

.set2 {
  margin-left: 1.5%;
}
.first {
  color: darkviolet;
  z-index: -1;
  animation: swag 3s cubic-bezier(0.175, 0.885, 0.32, 1.275) infinite 0.4s;
}

@-webkit-keyframes swag {
    form {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}

.flip-card-inner {
  transform-style: preserve-3d;
    transition: transform 0.6s;
 

}
.flip-card-front, .flip-card-back {
  position: relative;
  width: 100%;
  height: 100%;
    backface-visibility: visible;
}

.flip-card-front {
  background-color: #bbb;
  color: black;
  
}

.flip-card-back {
   background-color: #fff;
   display: block;
    width: 240px;
  height: 180px;
  cursor: grab;
  margin-right: 10px;
  
}
.flipped {
    -webkit-transform: rotateY( 180deg );
    -moz-transform: rotateY( 180deg );
    -o-transform: rotateY( 180deg );
    transform: rotateY( 180deg );
}
